import React, {useEffect, useState} from 'react';
import {CSSTransition} from "react-transition-group";
import Input from "../Input/Input";
import Container from "../Container/Container";
import styles from "./Converter.module.scss";
import Button from "../Button/Button";
import NoembedService from "../../API/NoembedService";

const Converter = () => {

    const [url, setUrl] = useState("");
    const [info, setInfo] = useState("");
    const [isExpanded, setExpanded] = useState(false);
    const [showContent, setShowContent] = useState(false);
    const [isInfoLoading, setInfoLoading] = useState(false);

    useEffect(() => {
        if (/^((?:https?:)?\/\/)?((?:www|m)\.)?(youtube\.com|youtu.be)(\/(?:[\w]+\?v=|embed\/|v\/)?)([\w]+)(\S+)?$/
            .test(url)) fetchVideoInfo(url);
    }, [url])

    function handleChange(input) {
        setUrl(input);
        setExpanded(true);
    }

    async function fetchVideoInfo(url) {
        const json = await NoembedService.getJson(url);
        setInfo(json);
    }

    return (
        <CSSTransition
            in={isExpanded}
            timeout={500}
            classNames={{
                enter: styles["Converter-enter"],
                enterActive: styles["Converter-enter-active"],
                enterDone: styles["Converter-enter-done"]
            }}
            onEntered={() => setShowContent(true)}
        >
            <form className={styles.Converter}>
                <Input
                    type="text"
                    placeholder="e.g. https://www.youtube.com/watch?v=dQw4w9WgXcQ"
                    value={url}
                    onChange={e => handleChange(e.target.value)}
                    onPaste={e => handleChange(e.target.value)}
                />
                <label className={styles.Converter__label} htmlFor={styles.Converter}>Enter youtube link:</label>
                <Container in={showContent} status={isInfoLoading} info={info}/>
            </form>
        </CSSTransition>
    );
};



export default Converter;